function rapidAnalysis() {

    function resultCalc(message, result, type, idBtn) {
        let resPlaceHolder = document.createElement("div");
        let btn = document.getElementById(idBtn);
        resPlaceHolder.innerHTML = `<div class="alert alert-${type} alert-dismissible fs-6 lh-sm" role="alert"> ${message} - ${result}%<button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button></div>`;
        btn.insertAdjacentElement("beforebegin", resPlaceHolder);
    }

    function resultCalcMoney(message, result, type, currency, idBtn) {
        let resPlaceHolder = document.createElement("div");
        let btn = document.getElementById(idBtn);
        resPlaceHolder.innerHTML = `<div class="alert alert-${type} alert-dismissible fs-6 lh-sm" role="alert"> ${message} - ${result} ${currency}<button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button></div>`;
        btn.insertAdjacentElement("beforebegin", resPlaceHolder);
    }

    const rallaSubmit = document.getElementById("rallaSubmit");
    rallaSubmit.addEventListener("click", () => {
        const str2400 = document.getElementById("str2400");
        const str1600s = document.getElementById("str1600start");
        const str1600e = document.getElementById("str1600end");
        let result = (+str2400.value / ((+str1600s.value + +str1600e.value) / 2) * 100).toFixed(2);

        let resultText = "";
        if (result <= 10) {
            resultText = "Ваша рентабельность активов и конкурентоспособность требует внимательного рассмотрения и составляет";
            resultCalc(resultText, result, "danger", "rallaSubmit");
        } else {
            resultText = "Ваша рентабельность активов находится на позитивном уровне и составляет";
            resultCalc(resultText, result, "success", "rallaSubmit");
        }
    });

    const rrpSubmit = document.getElementById("rrpSubmit");
    rrpSubmit.addEventListener("click", () => {
        const clearProfit = document.getElementById("clearProfit");
        const netAvails = document.getElementById("netAvails");
        let result = (+clearProfit.value / +netAvails.value * 100).toFixed(2);
        let resultText = "";
        if (result <= 5) {
            resultText = "Уделите внимание на средства, которые необходимы для расчета текущих расходов либо материально-производственных запасов. Ваша рентабельность реализации составляет";
            resultCalc(resultText, result, "danger", "rrpSubmit");
        } else if (result <= 10) {
            resultText = "Уделите внимание материально-производственным запасам. Ваша рентабельность реализации составляет";
            resultCalc(resultText, result, "warning", "rrpSubmit");
        } else {
            resultText = "Ваша рентабельность реализации находится в пределах нормы и составляет";
            resultCalc(resultText, result, "success", "rrpSubmit");
        }
    });

    const rskSubmit = document.getElementById("rskSubmit");
    rskSubmit.addEventListener("click", () => {
        const clearProfit = document.getElementById("rskClearProfit");
        const equityCapitalStart = document.getElementById("rskEquityCapitalStart");
        const equityCapitalEnd = document.getElementById("rskEquityCapitalEnd");
        let result = (+clearProfit.value / ((+equityCapitalStart.value + +equityCapitalEnd.value) / 2) * 100).toFixed(2);
        let resultText = "";
        if (result <= 5) {
            resultText = "Уделите внимание на норму прибыли или нехватки собственного капитала, найдите возможность привлечения заемных средств, либо на норму прибыли. Рентабельность собственного капитала составляет";
            resultCalc(resultText, result, "danger", "rskSubmit");
        } else if (result <= 10) {
            resultText = "Следует уделить внимание норме прибыли и возможностям инвестирования этой прибыли в собственный капитал. Рентабельность собственного капитала составляет";
            resultCalc(resultText, result, "warning", "rskSubmit");
        } else {
            resultText = "Ваша рентабельность собственного капитала находится в пределах нормы и составляет";
            resultCalc(resultText, result, "success", "rskSubmit");
        }
    });

    const kolSubmit = document.getElementById("kolSubmit");
    kolSubmit.addEventListener("click", (assets, liability) => {
        assets = document.getElementById("kolFloatingAssets");
        liability = document.getElementById("kolCurrentLiabilities");
        let result = (+assets.value / +liability.value * 100).toFixed(2);
        let resultText = "";
        if (result <= 100) {
            resultText = "Ваших оборотных средств достаточно или находятся на минимальном уровня для погашения Ваших краткосрочных обязательств. Общая ликвидность составляет";
            resultCalc(resultText, result, "warning", "kolSubmit");
        } else if (result <= 200) {
            resultText = "Ваших оборотных средств, более чем достаточно для погашения Ваших краткосрочных обязательств. Общая ликвидность составляет";
            resultCalc(resultText, result, "success", "kolSubmit");
        } else {
            resultText = "Прошу Вас обратить на увеличение запасов и готовой продукции на складах либо это говорит о нерациональной структуре капитала. Общая ликвидность составляет";
            resultCalc(resultText, result, "danger", "kolSubmit");
        }
    });

    const kslSubmit = document.getElementById("kslSubmit");
    kslSubmit.addEventListener("click", (assets, resource, liability) => {
        assets = document.getElementById("kslFloatingAssets");
        liability = document.getElementById("kslCurrentLiabilities");
        resource = document.getElementById("kslResource");
        let result = (((+assets.value - resource.value) / +liability.value) * 100).toFixed(2);
        let resultText = "";
        if (result <= 70) {
            resultText = "Ваших денежных средств не хватает для погашения краткосрочных обязательств. Необходимо объективно рассмотреть долговую нагрузку. Коэффициент срочной ликвидности составляет";
            resultCalc(resultText, result, "danger", "kslSubmit");
        } else {
            resultText = "Ваши денежные средства находятся на оптимальном уровне для погашения краткосрочных обязательств. Коэффициент срочной ликвидности составляет";
            resultCalc(resultText, result, "success", "kslSubmit");
        }
    });

    const chokSubmit = document.getElementById("chokSubmit");
    chokSubmit.addEventListener("click", (assets, liability, result) => {
        assets = document.getElementById("chokFloatingAssets");
        liability = document.getElementById("chokCurrentLiabilities");
        result = (+assets.value - +liability.value).toFixed(2);
        let resultText = "";
        if (result < 0) {
            resultText = "У Вас отрицательный результат. Прошу срочно принять меры так как это может привести к банкротству, он составляет";
            resultCalcMoney(resultText, result, "danger", "денежных единиц", "chokSubmit");
        } else {
            resultText = "У Вас оптимальная сумма оборотного капитала и составляет";
            resultCalcMoney(resultText, result, "success", "денежных единиц", "chokSubmit");
        }

    });

    const koaSubmit = document.getElementById("koaSubmit");
    koaSubmit.addEventListener("click", (netAvails, floatingAssetsStart, floatingAssetsEnd, resultText = "", result) => {
        netAvails = document.getElementById("koaNetAvails");
        floatingAssetsStart = document.getElementById("koaFloatingAssetsStart");
        floatingAssetsEnd = document.getElementById("koaFloatingAssetsEnd");
        result = Math.round(+netAvails.value / ((+floatingAssetsStart.value + +floatingAssetsEnd.value) / 2));
        resultText = "Оборачиваемость активов показывает, сколько раз в год реализованная продукция приносит эффект в виде прибыли. Период оборачиваемости активов составляет";
        resultCalcMoney(resultText, result, "info", "раз(а)", "koaSubmit");
    });

    const kodzSubmit = document.getElementById("kodzSubmit");
    kodzSubmit.addEventListener("click", (netAvails, notesPayableStart, notesPayableEnd, resultText = "", result) => {
        netAvails = document.getElementById("kodzNetAvails");
        notesPayableStart = document.getElementById("kodzNotesPayableStart");
        notesPayableEnd = document.getElementById("kodzNotesPayableEnd");
        result = parseInt(365 / +netAvails.value / ((+notesPayableStart.value + +notesPayableEnd.value) / 2));
        //resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для оплаты дебиторской задолженности, в связи с этим Вам требуется";
        //resultCalcMoney(resultText, result, "info", "дня(дней)", "kodzSubmit");
        if (result <= 1) {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для оплаты дебиторской задолженности, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "день", "kodzSubmit");
        } else if (result > 1 && result < 5) {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для оплаты дебиторской задолженности, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "дня", "kodzSubmit");
        } else {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для оплаты дебиторской задолженности, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "дней", "kodzSubmit");
        }
    });

    const kokzSubmit = document.getElementById("kokzSubmit");
    kokzSubmit.addEventListener("click", (pPrice, billPayStart, billPayEnd, resultText = "", result) => {
        pPrice = document.getElementById("kokzProductionPrice");
        billPayStart = document.getElementById("kokzBillPayableStart");
        billPayEnd = document.getElementById("kokzBillPayableEnd");
        result = parseInt(365 / +pPrice.value / ((+billPayStart.value + +billPayEnd.value) / 2));
        //resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для оплаты кредиторской задолженности, в связи с этим Вам требуется";
        //resultCalcMoney(resultText, result, "info", "дня(дней)", "kokzSubmit");
        if (result <= 1) {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для оплаты кредиторской задолженности, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "день", "kokzSubmit");
        } else if (result > 1 && result < 5) {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для оплаты кредиторской задолженности, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "дня", "kokzSubmit");
        } else {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для оплаты кредиторской задолженности, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "дней", "kokzSubmit");
        }
    });

    const kompzSubmit = document.getElementById("kompzSubmit");
    kompzSubmit.addEventListener("click", (pPrice, resourceStart, resourceEnd, resultText = "", result) => {
        pPrice = document.getElementById("kompzProductionPrice");
        resourceStart = document.getElementById("kompzResourceStart");
        resourceEnd = document.getElementById("kompzResourceEnd");
        result = parseInt(365 / +pPrice.value / ((+resourceStart.value + +resourceEnd.value) / 2));
        //resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для продажи (без оплаты) материально-производственных запасов, в связи с этим Вам требуется";
        //resultCalcMoney(resultText, result, "info", "дня(дней)", "kompzSubmit");
        if (result <= 1) {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для продажи (без оплаты) материально-производственных запасов, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "день", "kompzSubmit");
        } else if (result > 1 && result < 5) {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для продажи (без оплаты) материально-производственных запасов, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "дня", "kompzSubmit");
        } else {
            resultText = "При расчете этого показателя Вы можете узнать сколько дней Вам требуется для продажи (без оплаты) материально-производственных запасов, в связи с этим Вам требуется";
            resultCalcMoney(resultText, result, "info", "дней", "kompzSubmit");
        }
    });

    const ksoSubmit = document.getElementById("ksoSubmit");
    ksoSubmit.addEventListener("click", (capital, balance, result, resultText) => {
        capital = document.getElementById("ksoCapital");
        balance = document.getElementById("ksoBalance");
        result = (+capital.value / +balance.value * 100).toFixed(2);
        resultText = "";
        if (result < 60) {
            resultText = "Прошу Вас обратить на долю собственного капитала, так как ее низкий показатель может отпугнуть Ваших потенциальных инвесторов, в данный момент он составляет";
            resultCalc(resultText, result, "danger", "ksoSubmit");
        } else {
            resultText = "У вас достаточно стабильное финансовое положение при прочих равных условиях в глазах инвесторов и кредиторов. Ваш показатель составляет";
            resultCalc(resultText, result, "success", "ksoSubmit");
        }

    });

    const kfzSubmit = document.getElementById("kfzSubmit");
    kfzSubmit.addEventListener("click", (capital, longLiabilities, shortLiabilities, result, resultText = "") => {
        capital = document.getElementById("kfzCapital");
        longLiabilities = document.getElementById("kfzLongLiabilities");
        shortLiabilities = document.getElementById("kfzShortLiabilities");
        result = ((+longLiabilities.value + +shortLiabilities.value) / capital.value * 100).toFixed(2);
        if (result > 70) {
            resultText = "Высокий показатель этого коэффициента говорит о том, что это может затруднить с получением новых кредитов, кроме того происходит замедление реализации и увеличивается зависимость от внешних займов. Ваш показатель составляет";
            resultCalc(resultText, result, "danger", "kfzSubmit");
        } else if (result < 40) {
            resultText = "Вы ведете стабильную хозяйственную деятельность, зависимость от внешних финансов составляет";
            resultCalc(resultText, result, "success", "kfzSubmit");
        } else {
            resultText = "Возможен дефицит денежных средств, прошу обратить внимание на зависимость компании от внешних займов в размере";
            resultCalc(resultText, result, "warning", "kfzSubmit");
        }
    });

    const kzkSubmit = document.getElementById("kzkSubmit");
    kzkSubmit.addEventListener("click", (profit, tax, interest, result, resultText = "") => {
        profit = document.getElementById("kzkProfit");
        tax = document.getElementById("kzkProfitTax");
        interest = document.getElementById("kzkInterestPayable");
        result = Math.round((+profit.value + +tax.value + +interest.value) / +interest.value);
        resultText = "По этому показателю судят, сколько раз в течении периода компании заработала средства для выплаты процентов по займам и составляет";
        resultCalcMoney(resultText, result, "info", "раз(а)", "kzkSubmit");
    });
}

export default rapidAnalysis;